import React from 'react';
import ReactDOM from 'react-dom';
//import App from './App';
import { BrowserRouter, Route} from 'react-router-dom'; 
import Home from './Home'; 

const App = () => {

    return (
        <BrowserRouter>
            <Route path="/" exact component={Home} />
        </BrowserRouter>
    )
}

ReactDOM.render(
    <App />,
    document.getElementById("root")
)